	},
	onShow() {
		this.avatarUrl = uni.getStorageSync("headimg") == '' ?'http://img.zcool.cn/community/01460b57e4a6fa0000012e7ed75e83.png@1280w_1l_2o_100sh.png' : uni.getStorageSync("headimg")
		this.nickname = uni.getStorageSync("nickname") == '' ? '张三' : uni.getStorageSync("nickname")
	},
	onPullDownRefresh() {
		uni.stopPullDownRefresh()
	},
	onLoad() {
		that = this;










	},
	onTabItemTap(e) {

		this.$api.post(this.api.getCarNew, function callbacks(res) {
			if (res == -1) {
				that.$eventBus.$emit('register');
				uni.switchTab({url:'../home/home'})
			} 
		})

	},
	methods: {
		logout(){
			uni.clearStorageSync("isLogin")
		},
		showModalMP() {
			// console.log(that.modalMPShow);
			if (that.modalMPShow == 1) {
				that.modalMPShow = 0;
			} else {
				that.modalMPShow = 1;
			}
			console.log(that.modalMPShow);
		},
		getUserInfo(res) {
			// console.log(res)
			uni.setStorageSync('avatarUrl', res.detail.userInfo.avatarUrl)
		},
		navigateTo(url) {
			if (url == '4000851667') {
				uni.makePhoneCall({
					phoneNumber: url
				})
				return;
			}
			if (url == 'smhx') { //扫码核销

					uni.scanCode({
						success(res) {
							// console.log(res.result);
							let data = {
								vc_id: res.result
							};
							that.scanTask(data)
						},
						fail(res) {}
					})













				return;
			} else if (url == 'goMP') {
				that.showModalMP();
				// that.$api.msg('更多功能请至公众号体验')
				return;
			}
			uni.navigateTo({
				url: url
			})
		},
		scanTask(code) {
			that.$api.postWithData(that.api.scanTask, code,
				function callbacks(res) {
					if (res.code == 1) {
						that.scanTaskResult = res.data
						that.modalName = 'Modal'
						that.$api.msg(res.msg)
					} else {
						that.$api.msg(res.msg)
					}
				})
		}
	},
}
